// @mui
import { useEffect, useState } from 'react';
import { alpha } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import { DatePicker } from '@mui/x-date-pickers';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, Grid, InputAdornment, InputLabel, Menu, MenuItem, Modal, Select, TextField } from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import AddIcon from '@mui/icons-material/Add';
import SearchIcon from '@mui/icons-material/Search';
import PhoneIcon from '@mui/icons-material/Phone';
import { color, decryptToken, generateOfferCode, states } from 'src/utils/common';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';

import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';
import { useNavigate } from 'react-router';
import { useAuthContext } from 'src/auth/hooks';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '70%',
    backgroundColor: color.modalbackground,
    borderRadius: '10px',
    boxShadow: 24,
    p: 2,
    height: 'auto',
    overflow: 'auto',
};

const initialEmployeeData = {
    id: '',
    code: '',
    name: '',
    mobileNumber: '',
    dateOfBirth: null,
    address: {
        line1: '',
        line2: '',
        city: '',
        state: '',
        pincode: '',
    },
};

const initialCustomerErr = {
    name: false,
    email: false,
    mobileNumber: false,
    type: false,
    company: {
        name: false,
        gstNumber: false,
        billingAddress: {
            line1: false,
            line2: false,
            city: false,
            state: false,
            pincode: false,
        },
        shippingAddress: {
            line1: false,
            line2: false,
            city: false,
            state: false,
            pincode: false,
        },
    },
};

// ----------------------------------------------------------------------

export default function CustomerView() {
    const settings = useSettingsContext();
    const { enqueueSnackbar } = useSnackbar();
    const navigate = useNavigate();
    const { user } = useAuthContext();
    // For Modal Open
    const [open, setOpen] = useState(false);

    const [employeesLoading, setEmployeesLoading] = useState(false);
    const [employees, setEmployees] = useState([]);
    const [employee, setEmployee] = useState(initialEmployeeData);
    const [editUser, setEditUser] = useState(false);

    // Temporary State
    const [tempData, setTempData] = useState({});

    const [searchQuery, setSearchQuery] = useState('');

    const [employeeErr, setEmployeeErr] = useState({
        mobileNumber: false
    })

    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);


    // For Delete Employee
    const [openDialog, setOpenDialog] = useState(false);

    const handleOpen = () => {
        // const employeeCode = generateOfferCode();
        // setEmployee((prev) => ({ ...prev, code: employeeCode }));
        setOpen(true);
        setEditUser(false);
    };

    const handleClose = () => setOpen(false);


    const fetchData = async () => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.employee.list, {}, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setEmployeesLoading(false);
                setEmployees(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch employees!', { variant: 'error' });
            setEmployeesLoading(false);
        }
    };

    useEffect(() => {
        setEmployeesLoading(true);
        fetchData();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // You can use setFormData to update the state when user input changes
    const handleChange = (e) => {
        const { name, value } = e.target;
        setEmployee((prevFormData) => ({
            ...prevFormData,
            [name]: value,
        }));

    };


    const handleSaveEmployee = async () => {

        // If no errors, proceed with saving
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);

        // Create a new object without the id property
        const { id, ...data } = employee;

        data.dateOfBirth = dayjs(data.dateOfBirth).format('YYYY-MM-DD');

        if (!data.mobileNumber) {
            setEmployeeErr((prev) => ({ ...prev, mobileNumber: true }));
        } else if (!/^[0-9]*$/.test(data.mobileNumber) || data.mobileNumber.length !== 10) {
            setEmployeeErr((prev) => ({ ...prev, mobileNumber: true }));
        } else {
            setEmployeeErr((prev) => ({ ...prev, mobileNumber: false }));
        }

        axios.post(endpoints.employee.add, data,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            })
            .then((res) => {
                enqueueSnackbar('Employee created successfully!', { variant: 'success' });
                // fetchData();
                handleClose();
                // Reset the employee  state
                setEmployee(initialEmployeeData);

                fetchData();
            }).catch((err) => {
                enqueueSnackbar(err.error || err.msg, { variant: 'error' });
            })
    };

    const handleEditDetails = () => {
        setEditUser(true);
        setAnchorEl(null);
        const employeeData = {
            id: tempData._id,
            name: tempData.name,
            code: tempData.code,
            dateOfBirth: dayjs(tempData.dateOfBirth),
            mobileNumber: tempData.mobileNumber.slice(3),
            address: {
                line2: tempData?.address?.line2 ?? '',
                line1: tempData?.address?.line1 ?? '',
                city: tempData?.address?.city ?? '',
                state: tempData?.address?.state ?? '',
                pincode: tempData?.address?.pincode ?? '',
            },
        };
        setEmployee(employeeData);
        setOpen(true);
    };

    const handleDeleteDetails = () => {
        setEmployee((prev) => ({ ...prev, id: tempData._id }));
        setOpenDialog(true);
        setAnchorEl(null);
    };

    const handleConfirmDelete = () => {

        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        const config = {
            headers: {
                Authorization: `Bearer ${accessToken}`
            }
        };
        axios.post(endpoints.employee.delete, { id: employee.id }, config)
            .then((res) => {
                if (res) {
                    enqueueSnackbar('Employee deleted successfully!', { variant: 'success' });
                    setOpenDialog(false);
                    setEmployee(initialEmployeeData);
                    fetchData();
                };
            }).catch((err) => {
                enqueueSnackbar(err.msg || err.error, { variant: 'error' });
            })
    };

    const handleUpdateEmployee = async () => {
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);

        const data = { ...employee };
        data.dateOfBirth = dayjs(data.dateOfBirth).format('YYYY-MM-DD');

        if (!data.mobileNumber) {
            setEmployeeErr((prev) => ({ ...prev, mobileNumber: true }));
        } else if (!/^[0-9]*$/.test(data.mobileNumber) || data.mobileNumber.length !== 10) {
            setEmployeeErr((prev) => ({ ...prev, mobileNumber: true }));
        } else {
            setEmployeeErr((prev) => ({ ...prev, mobileNumber: false }));
        }

        axios.post(endpoints.employee.update, employee,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            })
            .then((res) => {
                enqueueSnackbar('Employee updated successfully!', { variant: 'success' });
                // fetchData();
                handleClose();
                // Reset the employee  state
                setEmployee(initialEmployeeData);
                fetchData();
            }).catch((err) => {
                enqueueSnackbar(err.error || err.msg, { variant: 'error' });
            })



    };

    const employeeColumns = [
        {
            name: 'Name',
            selector: row => row.name,
            sortable: true,
            cell: row => (
                <Typography
                    variant='subtitle2'
                    onClick={() => {
                        console.log({ row })
                        navigate('/dashboard/employee/details', { state: row });
                    }}
                    style={{ cursor: 'pointer' }} // Change cursor to pointer to indicate clickable
                >
                    {`${row.name.slice(0, 1).toUpperCase()}${row.name.slice(1).toLowerCase()}`}
                </Typography>
            ),
        },
        {
            name: 'Employee Code',
            selector: row => row.code,
        },
        {
            name: 'Contact Number',
            selector: row => row.mobileNumber,
        },
        {
            name: 'Quantities Sold',
            selector: row => row.soldQuantities,
            sortable: true,
            center: 'true'
        },
        {
            name: 'Sales Amount',
            selector: row => new Intl.NumberFormat('en-IN', {
                style: 'currency',
                currency: 'INR'
            }).format(row.totalSales),
            sortable: true,
        },
        {
            name: 'Actions',

            cell: (row) => (
                <>
                    <Button
                        disabled={!user?.permissions?.employee?.u}
                        onClick={(event) => {
                            setAnchorEl(event.currentTarget);
                            setTempData(row)
                        }}
                        style={{ color: color.actionButton }} variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={() => handleEditDetails()}>Edit Details</MenuItem>
                        <MenuItem onClick={() => handleDeleteDetails()}>Remove</MenuItem>
                    </Menu>
                </>
            ),
        },
    ];

    const filteredData = employees.filter((empData) =>
        empData?.name?.toLowerCase().includes(searchQuery?.toLowerCase())
        ||
        empData?.code?.toLowerCase().includes(searchQuery?.toLowerCase())
    );


    const handleAddressChange = (e) => {
        const { name, value } = e.target;
        // Restricting pincode to 6 digits
        if (name === 'pincode' && value.length > 6) {
            return;
        }
        setEmployee((prevFormData) => ({
            ...prevFormData,
            address: {
                ...prevFormData.address,
                [name]: value,
            },
        }));
    };


    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Typography variant="h4"> Employee Master </Typography>

            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '20px' }}>
                <TextField
                    size="small"
                    onChange={(e) => setSearchQuery(e.target.value)}
                    type="text"
                    placeholder="Search"
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="end">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
                {
                    user?.permissions?.employee?.u &&
                    <Button startIcon={<AddIcon />} color='primary' variant='contained' onClick={handleOpen}>
                        ADD EMPLOYEE
                    </Button>
                }

            </div>

            <Modal
                open={open}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>

                        <Typography id="modal-modal-title" variant="h4" component="h2">
                            Basic Details
                        </Typography>
                        <div style={{ padding: "10px", backgroundColor: 'white', borderRadius: '10px' }}>
                            <Grid container spacing={2} >
                                <Grid item xs={6}>
                                    <TextField
                                        label='Name'
                                        name='name'
                                        value={employee.name}
                                        onChange={handleChange}
                                        fullWidth
                                        required />
                                </Grid>

                                {
                                    editUser ?
                                        <Grid item xs={6}>
                                            <TextField
                                                label='Employee Code'
                                                name='code'
                                                value={employee.code}
                                                onChange={handleChange}
                                                fullWidth
                                                disabled
                                                required />
                                        </Grid>
                                        : null
                                }
                                {/* <Grid item xs={6}>
                                    <TextField
                                        label='Employee Code'
                                        name='code'
                                        value={employee.code}
                                        onChange={handleChange}
                                        fullWidth
                                        disabled
                                        required />
                                </Grid> */}
                                <Grid item xs={6}>
                                    <TextField
                                        label="Mobile Number"
                                        name="mobileNumber"
                                        value={employee.mobileNumber}
                                        onChange={handleChange}
                                        fullWidth
                                        error={employeeErr.mobileNumber}
                                        helperText={employeeErr.mobileNumber ? 'Please enter valid mobile number..' : null}
                                        required
                                        inputProps={{
                                            maxLength: 10,
                                        }}
                                        InputProps={{
                                            startAdornment: (
                                                <InputAdornment position="start">
                                                    <PhoneIcon /> +91
                                                </InputAdornment>
                                            )
                                        }}
                                    />

                                </Grid>
                                <Grid item xs={6}>
                                    <LocalizationProvider dateAdapter={AdapterDayjs} >
                                        <DatePicker
                                            label="Date of Birth"
                                            value={employee.dateOfBirth}
                                            onChange={(newValue) => setEmployee((prev) => ({ ...prev, dateOfBirth: newValue }))}
                                            slotProps={{ textField: { fullWidth: true, required: true } }}
                                            required
                                            format='DD/MM/YYYY'
                                        />
                                    </LocalizationProvider>
                                </Grid>

                            </Grid>
                        </div>

                        <div style={{ padding: "10px", backgroundColor: 'white', borderRadius: '10px' }}>
                            <Grid container spacing={2} >
                                <Grid item xs={12}>
                                    <Typography variant='h6'>Address</Typography>
                                </Grid>


                                <Grid item xs={6}>
                                    <TextField label='Address Line 1'
                                        // error={customerErr.company.billingAddress.line1}
                                        // helperText={customerErr.company.billingAddress.line1 ? 'Invalid line1 address!..' : null}
                                        value={employee.address.line1}
                                        name='line1'
                                        onChange={handleAddressChange}
                                        fullWidth
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField label='Address Line 2'
                                        // error={customerErr.company.billingAddress.line2}
                                        // helperText={customerErr.company.billingAddress.line2 ? 'Invalid line2 address!..' : null}
                                        value={employee.address.line2}
                                        name='line2'
                                        onChange={handleAddressChange}
                                        fullWidth
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField label='City'
                                        // error={customerErr.company.billingAddress.city}
                                        // helperText={customerErr.company.billingAddress.city ? 'Invalid city name!..' : null}
                                        value={employee.address.city}
                                        name='city'
                                        onChange={handleAddressChange}
                                        fullWidth
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <FormControl fullWidth>
                                        <InputLabel id="demo-simple-select-label">Select State</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            // error={customerErr.company.billingAddress.state}
                                            id="demo-simple-select"
                                            value={employee.address.state}
                                            name='state'
                                            onChange={handleAddressChange}
                                            label="Select State"

                                        >
                                            {
                                                states.map((state, index) => (
                                                    <MenuItem key={index} value={state}>{state}</MenuItem>
                                                ))
                                            }
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField
                                        label="Pincode"
                                        name="pincode"
                                        type='number'
                                        value={employee.address.pincode}
                                        // error={customerErr.company.billingAddress.pincode}
                                        // helperText={customerErr.company.billingAddress.pincode ? 'Invalid pincode!..' : null}

                                        onChange={handleAddressChange}
                                        fullWidth
                                        inputProps={{
                                            maxLength: 6, // Set maximum length to 4 characters
                                        }}
                                    />
                                </Grid>
                            </Grid>
                        </div>

                    </div>
                    <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'flex-end', padding: "10px" }}>
                        <Button
                            disabled={
                                !employee.name ||
                                !employee.mobileNumber ||
                                !employee.dateOfBirth
                            }
                            variant='contained'
                            color='primary'
                            onClick={editUser ? handleUpdateEmployee : handleSaveEmployee}
                        >
                            {editUser ? 'Update' : 'Save'}
                        </Button>
                        <Button variant='outlined' onClick={() => {
                            // setCustomerErr(initialCustomerErr);
                            if (editUser) {
                                setEmployee(initialEmployeeData);
                                setEditUser(false);
                            };
                            setOpen(false);
                            setTempData({});
                        }}>
                            Cancel
                        </Button>
                    </div>
                </Box>

            </Modal>

            <div style={{ marginTop: "20px" }}>
                <DataTable
                    columns={employeeColumns}
                    data={filteredData}
                    pagination
                    progressPending={employeesLoading}
                />
            </div>


            {/* Dialog For Delete */}
            <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
                <DialogTitle>Remove Employee</DialogTitle>
                <DialogContent>
                    Are you sure you want to delete this employee?
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setOpenDialog(false)}>Cancel</Button>
                    <Button onClick={handleConfirmDelete} variant="contained" color="error">
                        Delete
                    </Button>
                </DialogActions>
            </Dialog>

        </Container>
    );
}
